body {
  font-family: "Montserrat", sans-serif;
  font-size: 17px;
  font-weight: 300;
  color: #626262;
  line-height: 1.53; }

.color1 {
  color: #927faf; }

.color2 {
  color: #d490c3; }

.color3 {
  color: #749ea7; }

/* `Link & btn
-------------------------------------------------------------*/
a {
  text-decoration: none;
  color: #333333; }

button {
  font-size: 1em;
  text-align: center; }

.button {
  font-weight: 500;
  text-align: center;
  padding: 1em 1.3em;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  width: 200px;
  margin: 40px auto 0;
  position: relative; }
  .button.type2 {
    color: #d490c3; }
    .button.type2.type2:after, .button.type2.type2:before {
      content: '';
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #d490c3;
      transition: all 0.3s ease;
      transform: scale(0.85); }
  .button.type2:hover:before {
    top: 0;
    transform: scale(1); }
  .button.type2:hover:after {
    transform: scale(1); }

.button--erreur {
  width: 160px;
  margin: 30px auto 0; }

/* `Title
-------------------------------------------------------------*/
h1, h2, h3 {
  color: #ffffff; }

h1, h2 {
  font-weight: 600;
  font-size: 10vw; }

h1 {
  margin: 0; }
  @media (min-width: 750px) {
    h1 {
      margin: 0 0 30px; } }

.titre-404 {
  font-size: 15vh;
  margin: 0; }

/* Homepage
-------------------------------------------------------------*/
.logo {
  font-size: 1em;
  font-weight: 500;
  line-height: 50px; }

.logo span {
  font-size: 0.9411764706em;
  font-weight: 300;
  cursor: default; }

/* Textes paragraphes */
p {
  margin: 0 0 30px; }

.text {
  max-width: 388px; }

.text p:last-child {
  margin-bottom: 0; }

.text--center {
  margin: 0 auto; }

.links {
  font-size: 1.1764705882em;
  font-weight: 400;
  transition: 0.5s; }

.links a:hover {
  color: #927faf; }

.links--javascript a:hover {
  color: #d490c3; }

.links--multi a:hover {
  color: #749ea7; }

/* Layout
-------------------------------------------------------------*/
.hidden {
  display: none; }

.overflowy {
  overflow-y: hidden; }

.bg-color1 {
  background-color: #e8dbfb; }

.bg-color2 {
  background-color: #ffe2f6; }

.bg-color3 {
  background-color: #d7e5e8; }

.container {
  width: 90%;
  margin: 0 auto; }
  @media (min-width: 998px) {
    .container {
      width: 998px; } }

.header {
  width: calc(100% - 20px);
  height: 50px;
  margin: 0 auto;
  background-color: #fff;
  position: relative; }
  @media (min-width: 750px) {
    .header {
      position: relative;
      width: calc(100% - 100px); } }

/* Views
-------------------------------------------------------------*/
.vs-center-wrap {
  display: table;
  width: 100%;
  height: 100%;
  table-layout: fixed; }

.vs-center {
  display: table-cell;
  vertical-align: middle;
  text-align: center; }

.mainbag {
  position: relative;
  height: 100%;
  width: 100%; }

.mainview {
  position: relative;
  height: 100%;
  width: 100%;
  vertical-align: bottom;
  white-space: normal; }

/* Views style */
.view6 {
  background: url(assets/img/binary-bg.svg) no-repeat 23px 15px, url(assets/img/binary-bg.svg) no-repeat calc(100% - 23px) calc(100% - 15px) #ffe2f6;
  background-size: auto 20vh; }
  @media (min-width: 750px) {
    .view6 {
      background: url(assets/img/binary-bg.svg) no-repeat 65px 65px, url(assets/img/binary-bg.svg) no-repeat calc(100% - 65px) calc(100% - 65px) #ffe2f6;
      background-size: auto 22vh; } }

/* Borders
-------------------------------------------------------------*/
.borders > * {
  position: fixed;
  z-index: 2;
  background: #ffffff; }

.borders .top {
  top: 0;
  width: 100vw;
  height: 50px;
  display: none; }
  @media (min-width: 750px) {
    .borders .top {
      display: block; } }

.borders .bottom {
  bottom: 0;
  width: 100vw;
  height: 50px;
  display: none; }
  @media (min-width: 750px) {
    .borders .bottom {
      display: block; } }

.borders .left {
  top: 0;
  left: 0;
  height: 100vh;
  width: 10px; }
  @media (min-width: 750px) {
    .borders .left {
      width: 50px; } }

.borders .right {
  top: 0;
  right: 0;
  height: 100vh;
  width: 10px; }
  @media (min-width: 750px) {
    .borders .right {
      width: 50px; } }

.borders .top--erreur, .borders .bottom--erreur {
  display: block; }

/* Aside elements
-------------------------------------------------------------*/
.logo {
  position: relative; }

@media (min-width: 750px) {
  .logo--home {
    position: fixed;
    top: 0;
    height: 50px;
    left: 50px;
    z-index: 4; } }

.logo span {
  padding-left: 12px; }

.bloc-bg {
  width: calc(100% - 20px);
  height: 160px;
  margin: 0 auto 50px;
  position: relative; }
  @media (min-width: 400px) {
    .bloc-bg {
      margin: 0 auto 80px;
      height: 200px; } }
  @media (min-width: 750px) {
    .bloc-bg {
      margin: 0 auto 100px;
      width: calc(100% - 100px); } }
  @media (min-width: 460px) {
    .bloc-bg {
      height: 240px; } }

.links {
  margin: 0 auto 50px; }
  @media (min-width: 750px) {
    .links {
      margin: 0 auto 100px; } }

.center {
  text-align: center; }

/* `Animation
-------------------------------------------------------------*/
/* Animations lettres svg */
.fill {
  width: 160px;
  position: absolute;
  left: calc(50% - 75px);
  bottom: -85px; }

@media (min-width: 400px) {
  .fill {
    width: 200px;
    position: absolute;
    left: calc(50% - 100px);
    bottom: -110px; } }

@media (min-width: 460px) {
  .fill {
    width: 250px;
    position: absolute;
    left: calc(50% - 115px);
    bottom: -130px; } }

.fill path, .fill--hof polyline, .fill--musicplz polyline {
  animation: fill 2.5s ease-out forwards; }

@keyframes fill {
  to {
    stroke-dashoffset: 0; } }

.fill path {
  stroke-dasharray: 664;
  stroke-dashoffset: 664; }

.fill--hof polyline {
  stroke-dasharray: 608;
  stroke-dashoffset: 608; }

.fill--musicplz {
  width: 180px;
  left: calc(50% - 90px);
  bottom: -95px; }
  @media (min-width: 400px) {
    .fill--musicplz {
      width: 230px;
      left: calc(50% - 115px);
      bottom: -130px; } }
  @media (min-width: 460px) {
    .fill--musicplz {
      width: 320px;
      left: calc(50% - 160px);
      bottom: -150px; } }

.fill--musicplz polyline {
  stroke-dasharray: 756;
  stroke-dashoffset: 756; }
