body {
    font-family: $baseFont;
	font-size: 17px;
    font-weight: 300;
	color: $mainColor;
	line-height: 1.53;
}

.color1 {color: $fontColor1; }
.color2 { color: $fontColor2; }
.color3 {color: $fontColor3; }

/* `Link & btn
-------------------------------------------------------------*/

a {
    text-decoration: none;
    color: $mainDark;
}

button {
    font-size: 1em;
    text-align: center;
}

.button {
    font-weight: 500;
    text-align: center;
    padding: 1em 1.3em;
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    width: 200px;
    margin: 40px auto 0;
    position: relative;
    
    &.type2 {
        color: $fontColor2;
        
        &.type2:after,
        &.type2:before {
          content: '';
          display: block;
          position: absolute;
          top: 100%;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: $fontColor2;
          transition: all 0.3s ease;
          transform: scale(0.85);
          }
    }

    &.type2:hover:before {
          top: 0;
          transform: scale(1);
    }

    &.type2:hover:after {
          transform: scale(1);
    }
}

.button--erreur {
    width: 160px;
    margin: 30px auto 0;
}
	

/* `Title
-------------------------------------------------------------*/

h1, h2, h3 {
    color: #ffffff;
}

h1, h2 {
    font-weight: 600;
    font-size: 10vw;

}
    
h1 {
    margin: 0;
    
    @media (min-width: $breakpoint1) {
        margin: 0 0 30px;
    }
}

.titre-404 {
    font-size: 15vh;
    margin: 0;
}


/* Homepage
-------------------------------------------------------------*/

.logo {
    font-size: 1em;
    font-weight: 500;
    line-height: 50px;
}

.logo span{
    font-size: 0.9411764706em;
    font-weight: 300;
    cursor: default;
}

/* Textes paragraphes */

p {
    margin: 0 0 30px;
}

.text {
    max-width: 388px;
}

.text p:last-child {
    margin-bottom: 0;
}

.text--center {
    margin: 0 auto;
}

.links {
    font-size: 1.1764705882em;
    font-weight: 400;
    transition: 0.5s;
}

.links a:hover {
    color: $fontColor1;
}

.links--javascript a:hover {
    color: $fontColor2;
}

.links--multi a:hover {
    color: $fontColor3;
}



