/* Layout
-------------------------------------------------------------*/

.hidden {
    display: none;
}

.overflowy {
    overflow-y: hidden;
}

.bg-color1 {background-color: $bgColor1;}
.bg-color2 {background-color: $bgColor2;}
.bg-color3 {background-color: $bgColor3;}


.container {
    width: 90%;
    margin: 0 auto;
    
    @media (min-width: 998px) {
        width: 998px;
    }
}

.header {
    width: calc(100% - 20px);
    height: 50px;
    margin: 0 auto;
    background-color: #fff;
    position: relative;
    
    @media (min-width: $breakpoint1) {
        position: relative;
        width: calc(100% - 100px);
    }
}



/* Views
-------------------------------------------------------------*/

.vs-center-wrap {
    display: table;
    width: 100%;
    height: 100%;
    table-layout: fixed;
}

.vs-center {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}

.mainbag {
    position: relative;
    height: 100%;
    width: 100%;
}

.mainview {
    position: relative;
    height: 100%;
    width: 100%;
    vertical-align: bottom;
    white-space: normal;
}

/* Views style */

.view6 {
    background: url(assets/img/binary-bg.svg) no-repeat 23px 15px, url(assets/img/binary-bg.svg) no-repeat calc(100% - 23px) calc(100% - 15px) $bgColor2;
    background-size: auto 20vh;
    
    @media (min-width: $breakpoint1) {
        background: url(assets/img/binary-bg.svg) no-repeat 65px 65px, url(assets/img/binary-bg.svg) no-repeat calc(100% - 65px) calc(100% - 65px) $bgColor2;
        background-size: auto 22vh;
    }
}

/* Borders
-------------------------------------------------------------*/

.borders > * {
    position: fixed;
    z-index: 2;
    background: #ffffff;
}

.borders .top {
    top: 0;
    width: 100vw;
    height: 50px;  
    display: none;
    
    @media (min-width: $breakpoint1) {
        display: block;
    }
}

.borders .bottom {
    bottom: 0;
    width: 100vw;
    height: 50px;
    display: none;
    
    @media (min-width: $breakpoint1) {
        display: block;
    }
}

.borders .left {
    top: 0;
    left: 0;
    height: 100vh;
    width: 10px;
    
    @media (min-width: $breakpoint1) {
        width: 50px;
    }
}

.borders .right {
    top: 0;
    right: 0;
    height: 100vh;
    width: 10px;
    
    @media (min-width: $breakpoint1) {
        width: 50px;
    }
}

.borders .top--erreur, .borders .bottom--erreur {
    display: block;
}

.borders .top--erreur {
    @media (min-width: 750px){
        
    }
}


/* Aside elements
-------------------------------------------------------------*/

.logo {
    position: relative;
}

.logo--home {
    @media (min-width: $breakpoint1) {
        position: fixed;
        top: 0;
        height: 50px;
        left: 50px;
        z-index: 4;
    }
}

.logo span{
    padding-left: 12px;
}



.bloc-bg {
    width: calc(100% - 20px);
    height: 160px;
    margin: 0 auto 50px;
    position: relative;
    
    @media (min-width: 400px) {
        margin: 0 auto 80px;
        height: 200px;
    }
    
    @media (min-width: $breakpoint1) {
        margin: 0 auto 100px;
        width: calc(100% - 100px);
    }
    
    @media (min-width: $breakpoint2) {
        height: 240px;
    }
}

.links {
    margin: 0 auto 50px;
    
    @media (min-width: 750px) {
        margin: 0 auto 100px;
    }
}

.center {
    text-align: center;
}



